.administrator-controls-page .policies .row{
    margin:7.5% 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
}

.administrator-controls-page .policies .row1{
    padding: 0 0 0 10%;
}

.administrator-controls-page .policies .row2{
    padding: 0 10% 0 0;
}

.administrator-controls-page .policies .row{
    .img{
        content: url('../../../../assets/AdministratorControls/img2.png');
        width: 50%;
    }
    .info{
        width: 42.5%;
    }
    h1{
        font-size: 33px;
    }
    p{
        font-size: 19px;
        margin:15px 0 35px;
    }
    .items2{
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        margin-bottom: 50px;
    }
    .item2{
        width: 30%;
        svg{
            color:#427cff;
            font-size: 55px;
            margin-bottom: 15px;
        }
        h6{
            font-size: 18px;
        }
    }
    a{
        font-size: 18px;
        padding:15px 40px;
        border-radius: 25px;
        background: #427cff;
        color:#fff;
        clear:both;
        transition:all 0.75s;
        &:hover{
            box-shadow: 0 80px 0 0  rgba(0,0,0,0.25) inset, 0 -80px 0 0  rgba(0,0,0,0.25) inset;
        }
    }
}

@media(max-width:1401px){
    .administrator-controls-page .policies .row1{
        padding: 0 0 0 7.5%;
    }
    
    .administrator-controls-page .policies .row2{
        padding: 0 7.5% 0 0;
    }
    
    .administrator-controls-page .policies .row{
        .img{
            width: 47.5%;
        }
        .info{
            width: 45%;
        }
        h1{
            font-size: 25px;
        }
        p{
            font-size: 16px;
            margin:15px 0 30px;
        }
        .items2{
            margin-bottom: 45px;
        }
        .item2{
            width: 30%;
            svg{
                font-size: 45px;
                margin-bottom: 15px;
            }
            h6{
                font-size: 15px;
            }
        }
        a{
            font-size: 15px;
            padding:15px 40px;
        }
    }
    
}

@media(max-width:991px){
    .administrator-controls-page .policies .row1{
        padding: 0 7.5%;
    }
    
    .administrator-controls-page .policies .row2{
        padding: 0 7.5%;
    }
    
    .administrator-controls-page .policies .row{
        .img{
            width: 400px;
            max-width: 100%;
            margin:20px auto;
        }
        .info{
            width: 100%;
            margin:20px 0;
        }
    }
}

@media(max-width:500px){
    .administrator-controls-page .policies .row1{
        flex-direction: column-reverse;
    }
        
    .administrator-controls-page .policies .row{
        h1{
            font-size: 20px;
        }
        p{
            margin:10px 0 20px;
        }
        .items2{
            margin-bottom: 30px;
        }
        .item2{
            width: 100%;
            margin:10px 0;
            svg{
                font-size: 40px;
                margin-bottom: 7.5px;
            }
        }
    }
}

@-webkit-keyframes hvr-icon-up {
    0%,
      50%,
      100% {
      -webkit-transform: translateY(0);
      transform: translateY(0);
    }
    25%,
      75% {
      -webkit-transform: translateY(-6px);
      transform: translateY(-6px);
    }
  }
  @keyframes hvr-icon-up {
    0%,
      50%,
      100% {
      -webkit-transform: translateY(0);
      transform: translateY(0);
    }
    25%,
      75% {
      -webkit-transform: translateY(-6px);
      transform: translateY(-6px);
    }
  }
.administrator-controls-page .policies .row{
    .item2:hover svg{
        -webkit-animation-name: hvr-icon-up;
        animation-name: hvr-icon-up;
        -webkit-animation-duration: 1s;
        animation-duration: 1s;
        -webkit-animation-timing-function: ease-out;
        animation-timing-function: ease-out;
        animation-iteration-count: infinite;
    }
}