.administrator-controls-page .dashboard-row{
    margin:5% 0;
    padding:0 7.5% 0 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    .img{
        content: url('../../../../assets/AdministratorControls/img1.png');
        width: 45%;
    }
    .items-container{
        width: 50%;
    }
    h1{
        font-size: 30px;
        margin-bottom: 35px;
    }
    .items1{
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
    }
    .item1{
        border-radius: 10px;
        width: 47.5%;
        padding:40px 25px;
        margin:10px 0;
        &:hover{
            background: linear-gradient(#427CFF, #213E80);
            p,h6, svg{
                color:#fff;
            }
        }
    }
    svg{
        color:#427cff;
        font-size: 45px;
    }
    h6{
        font-size: 18px;
        margin:17.5px 0;
    }
    p{
        font-size: 18px;
    }
}

@media(max-width:1401px){
    .administrator-controls-page .dashboard-row{
        .img{
            width: 45%;
        }
        .items-container{
            width: 50%;
        }
        h1{
            font-size: 25px;
            margin:25px 15px 10px;
        }
        .item1{
            padding:25px 20px;
            margin:5px 0;
        }
        svg{
            font-size: 40px;
        }
        h6{
            font-size: 15px;
            margin:15px 0;
        }
        p{
            font-size: 15px;
        }
    }
}

@media(max-width:1000px){
    .administrator-controls-page .dashboard-row{
        flex-wrap: wrap;
        padding:0 7.5%;
        .img{
            width: 450px;
            max-width: 90%;
            margin:20px auto;
        }
        .items-container{
            width: 590px;
            max-width: 100%;
            margin:10px auto;
        }
    }
}

@media(max-width:500px){
    .administrator-controls-page .dashboard-row{
        margin:30px 0 10px;
        h1{
            font-size: 20px;
        }
        .item1{
            width:100%;
            padding:15px;
        }
    }
}